<template>
    <a-space>
        <icon-font :type="type" class="iconfont" style="font-size: 18px" />
    </a-space>
</template>
<script>
import { createFromIconfontCN } from "@ant-design/icons-vue";
import { ref } from "vue";

// iconfont url
const IconFont = createFromIconfontCN({
    scriptUrl: "//at.alicdn.com/t/c/font_4729444_ili4k845fz.js",
});
export default {
    name: "Iconfont", // icon-font
    components: { IconFont },
    props: {
        type: {
            default: "",
            type: String,
        },
    },
    setup(ctx) {
        const type = ref(ctx.type);
        return { type };
    },
};
</script>
